import React, { useContext, useState, useEffect } from 'react'
import { Context } from "../index"
import { HOME_ROUTE, CART_ROUTE, PROFILE_ROUTE, DELIVERY_ROUTE, POLICY_ROUTE } from "../utils/consts"
import { Link } from "react-router-dom"
import { observer } from "mobx-react-lite"
import { setOrder } from "../http/orderAPI"
import CartContent from "../components/Cart"
import InputMask from 'react-input-mask'
import { AddressSuggestions } from 'react-dadata'
import { NotificationManager } from 'react-notifications'
import { NavDropdown } from "react-bootstrap"

const Checkout = observer(() => {
    const { user, cart } = useContext(Context)
    const [sendOrder, setSendOrder] = useState(false);
    const timeNew = new Date()
    const timeNow = new Date()
    timeNew.setTime(timeNew.getTime() + 90 * 60 * 1000)
    const time = timeNew.getHours() + ':' + (timeNew.getMinutes() < 10 ? '0' : '') + timeNew.getMinutes()
    const [checkout, setCheckout] = useState({
        name: (user.isAuth && user.user.firstname) ? user.user.firstname : '',
        phone: (user.isAuth && user.user.phone) ? user.user.phone : '',
        user: (user.isAuth) ? user.user.id : '',
        time: 1,
        timevalue: time,
        payment: 'card',
        delivery: 1,
        total: cart.total,
        products: cart.cart,
        full: '', street: '', home: '', entrance: '', code: '', floor: '', apartment: '',
        address: (user.user.address && user.user.address[0]) ? user.user.address[0] : '',
        saveaddress: true,
        comment: '',
        sale: cart.sale
    })
    const localAddress = localStorage.getItem('address')
    const [address, setAddress] = useState((localAddress) ? localAddress : ' 97  XL')


    useEffect(() => {
        document.title = " "
    }, [])

    const addLocalAddress = (e) => {
        setAddress(e.target.innerText)
        localStorage.setItem('address', e.target.innerText)
    }
    const updateTotal = () => {
        if (cart.total < cart.deliveryMinPrice && checkout.delivery == 2) {
            checkout.total = cart.total + cart.deliveryPrice
        } else {
            checkout.total = cart.total
        }
        setCheckout({ ...checkout, total: checkout.total })
    }
    useEffect(() => {
        updateTotal()
    }, [checkout.delivery, cart.total])

    const submit = async (e) => {
        e.preventDefault()
        if (checkout.delivery == 2) {
            if (checkout.street.length < 1) {
                NotificationManager.error('  ')
                return
            }
            if (checkout.home.length < 1) {
                NotificationManager.error('  ')
                return
            }
            if (checkout.apartment.length < 1) {
                NotificationManager.error('  ')
                return
            }
        }
        let data = await setOrder(checkout)
        if (data && data.Success && data.PaymentURL) {
            setSendOrder(true)
            cart.removeAllCart()
            window.location = data.PaymentURL
        } else if (data) {
            setSendOrder(true)
            cart.removeAllCart()
        } else {
            NotificationManager.error('  ,   ')
        }
    }
    const change = (e) => {
        if (e.target.name === 'address') {
            setCheckout({ ...checkout, [e.target.name]: user.user.address[e.target.value] })
        } else if (e.target.name === 'saveaddress') {
            setCheckout({ ...checkout, [e.target.name]: e.target.checked })
        } else {
            setCheckout({ ...checkout, [e.target.name]: e.target.value })
        }
    }
    const changeAddress = (data) => {
        if (data) {
            setCheckout({
                ...checkout,
                full: (data.value) ? data.value : '',
                street: (data.data.street_with_type) ? data.data.street_with_type : '', //
                home: (data.data.house) ? data.data.house : '', //
                entrance: (data.data.entrance) ? data.data.entrance : '', //
                apartment: (data.data.flat) ? data.data.flat : '' //
            })
        }
    }
    if (sendOrder) {
        return (
            <main>
                <section className="mt-5 mb-5">
                    <div className="container">
                        <div className="row justify-content-center">
                            <div className="col-md-7 col-lg-6 col-xl-5">
                                <h2 className="text-center">  !</h2>
                                <div className="text-center mb-4 mb-sm-5">    2  15 .       .</div>
                                <Link to={HOME_ROUTE} className="btn btn-2 mx-auto py-md-3"> </Link>
                            </div>
                        </div>
                    </div>
                </section>
            </main>
        )
    }
    return (
        <main>
            <div className="container mb-4 mb-md-5">
                <nav aria-label="breadcrumb">
                    <ol className="breadcrumb mb-0">
                        <li className="breadcrumb-item"><Link to={HOME_ROUTE}></Link></li>
                        <li className="breadcrumb-item"><a> </a></li>
                    </ol>
                </nav>
            </div>
            {
                (timeNow.getHours() === 22 || timeNow.getHours() < 10) ?
                    <section className="mb-8">
                        <div className="container">
                            <div className="row justify-content-center">
                                <div className="col-md-7 col-lg-6 col-xl-5">
                                    <h2 className="text-center">   10:00  22:00</h2>
                                    <div className="text-center mb-4 mb-sm-5">           ,  .</div>
                                    <Link to={HOME_ROUTE} className="btn btn-2 mx-auto py-md-3"> </Link>
                                </div>
                            </div>
                        </div>
                    </section>
                    :
                    <section id="sec-14" className="mb-8">
                        <div className="container">
                            <form onSubmit={submit} className="mb-4 mb-sm-5">
                                <h1> </h1>
                                <div className="row justify-content-between gx-4 gx-xl-5">
                                    <div className="col-md-7 col-xl-8 col-xxl-7">
                                        <div className="row mb-3">
                                            <label for="inputEmail3" className="col-sm-2 col-form-label">:</label>
                                            <div className="col-sm-10">
                                                <input type="text" placeholder=" " name="name" defaultValue={checkout.name} onChange={change} className="mb-3" />
                                            </div>
                                            <label for="inputEmail3" className="col-sm-2 col-form-label">:</label>
                                            <div className="col-sm-10">
                                                <InputMask mask="+7 999 999 99 99" placeholder="+7 000 000 00 00" name="phone" maskChar="" defaultValue={checkout.phone} onChange={change} className="mb-3" />
                                            </div>
                                        </div>
                                        <fieldset className="mb-4 mb-sm-5">
                                            <legend className="title-font gray-1 fs-15 fw-7 mb-3">  :</legend>
                                            <div>
                                                <div className="switch fs-11 mb-4">
                                                    <a className={checkout.delivery === 1 ? "switch-option fw-6 active" : "switch-option fw-6"} onClick={() => setCheckout({ ...checkout, delivery: 1 })}></a>
                                                    <a className={checkout.delivery === 2 ? "switch-option fw-6 active" : "switch-option fw-6"} onClick={() => setCheckout({ ...checkout, delivery: 2 })}></a>
                                                </div>

                                                {checkout.delivery === 1 ?
                                                    <div className="row g-2 g-lg-3 sec-font pl-2 pr-2 pt-2">
                                                        <NavDropdown
                                                            title={address}
                                                            className="fw-6 fs-11 address-menu"
                                                        >
                                                            <NavDropdown.Item onClick={addLocalAddress} active={(address == ' 97  XL') ? true : false}> 97  XL</NavDropdown.Item>
                                                            <NavDropdown.Item onClick={addLocalAddress} active={(address == ' 33') ? true : false}> 33</NavDropdown.Item>
                                                        </NavDropdown>
                                                    </div>
                                                    :
                                                    (user.isAuth && user.user.address && user.user.address.length > 0) ?
                                                        <>
                                                            <div className="fs-09 gray-1 mb-4">     .</div>
                                                            {
                                                                user.user.address.map((item, i) =>
                                                                    <div key={i} className="d-flex align-items-start mb-4">
                                                                        <input type="radio" name="address" value={i} id={"address-" + i} onClick={change} defaultChecked={(i === 0) ?? true} />
                                                                        <div className="ms-2">
                                                                            <label for={"address-" + i} className="gray-1 fw-5">{item.name}</label>
                                                                            <div className="d-flex mt-2">
                                                                                <Link to={PROFILE_ROUTE + '/address/edit/' + item.id} className="fs-09 gray-4"></Link>
                                                                            </div>
                                                                        </div>
                                                                    </div>
                                                                )
                                                            }
                                                            <Link to={PROFILE_ROUTE + '/address/add'} className="d-flex align-items-center">
                                                                <img src="/images/icons/plus3.svg" alt="" className="me-2" />
                                                                <span className="primary"> </span>
                                                            </Link>
                                                        </>
                                                        :
                                                        <div className="row g-2 g-lg-3 sec-font">
                                                            <div className="col-sm-10">
                                                                <div className="gray-1 mb-2"> <span className="text-danger">*</span></div>
                                                                <AddressSuggestions inputProps={{ autoComplete: "new-address", placeholder: "" }} name="street" token={process.env.REACT_APP_DADATA_API} filterLocations={[{ "kladr_id": "16" }]} count={6} minChars={1} delay={500} selectOnBlur={true} defaultQuery={checkout.full} onChange={changeAddress} />
                                                            </div>
                                                            <div className="col-2">
                                                                <div className="gray-1 mb-2"> <span className="text-danger">*</span></div>
                                                                <input type="text" name="home" placeholder="" onChange={change} value={checkout.home} />
                                                            </div>
                                                            <div className="col-3">
                                                                <div className="gray-1 mb-2"> <span className="text-danger">*</span></div>
                                                                <input type="text" name="apartment" placeholder="" onChange={change} value={checkout.apartment} />
                                                            </div>
                                                            <div className="col-3">
                                                                <div className="gray-1 mb-2"></div>
                                                                <input type="text" name="entrance" placeholder="" onChange={change} value={checkout.entrance} />
                                                            </div>
                                                            <div className="col-3">
                                                                <div className="gray-1 mb-2"></div>
                                                                <input type="text" name="floor" placeholder="" onChange={change} value={checkout.floor} />
                                                            </div>
                                                            <div className="col-3">
                                                                <div className="gray-1 mb-2"> </div>
                                                                <input type="text" name="code" placeholder=" " onChange={change} value={checkout.code} />
                                                            </div>
                                                            <div className="col-12">
                                                                <div className="gray-1 mb-2"></div>
                                                                <textarea rows="3" name="comment" placeholder="  " onChange={change} value={checkout.comment}></textarea>
                                                                <div className="mt-2"><span className="text-danger">*</span> -    </div>
                                                            </div>
                                                            {
                                                                (user.isAuth) ?
                                                                    <div className="col-12">
                                                                        <div className="d-flex align-items-center">
                                                                            <input type="checkbox" name="saveaddress" id="save-address" checked={checkout.saveaddress} onChange={change} />
                                                                            <label for="save-address" className="ms-3">     </label>
                                                                        </div>
                                                                    </div>
                                                                    : null
                                                            }

                                                        </div>
                                                }
                                            </div>
                                        </fieldset>
                                        <fieldset className="mb-4 mb-sm-5">
                                            <legend className="title-font gray-1 fs-15 fw-7 mb-3"> :</legend>
                                            <div className=" sec-font ">
                                                <div className="d-flex align-items-center mb-2">
                                                    <input type="radio" name="time" value={1} id="sooner" onClick={change} defaultChecked />
                                                    <label for="sooner" className="flex-1 ms-2">  </label>
                                                </div>
                                                <div className="d-flex align-items-center">
                                                    <input type="radio" name="time" value={2} onClick={change} id="in-time" />
                                                    <label for="in-time" className="ms-2">   </label>
                                                    <input type="time" name="timevalue" className="ms-2 w-fit py-2" onChange={change} defaultValue={time} />
                                                </div>
                                            </div>
                                        </fieldset>
                                        <fieldset>
                                            <legend className="title-font gray-1 fs-15 fw-7 mb-3"> :</legend>
                                            <div className="sec-font">
                                                <div>
                                                    <div className="d-flex align-items-center mb-3">
                                                        <input type="radio" name="payment" value="card" id="card" onClick={change} defaultChecked />
                                                        <label for="card" className="ms-2"> </label>
                                                    </div>
                                                    <div className="d-flex align-items-center mb-3">
                                                        <input type="radio" name="payment" value="cash" onClick={change} id="cash" />
                                                        <label for="cash" className="ms-2"></label>
                                                    </div>
                                                    <div className="d-flex align-items-center">
                                                        <input type="radio" name="payment" onClick={change} value="online" id="online" />
                                                        <label for="online" className="ms-2"> </label>
                                                    </div>
                                                </div>
                                            </div>
                                        </fieldset>
                                    </div>
                                    <div className="col-md-5 col-xl-4 position-relative">
                                        <div className="bonus">
                                            <div className="gray-1 title-font fw-7 fs-12 lh-15 text-center">   700  -  !<br /><Link className="text-success" to={DELIVERY_ROUTE}>  </Link></div>
                                        </div>
                                        <div className="checkout-box cart-preview">
                                            <div className="mb-4">
                                                <div className="title-font gray-1 fs-15 fw-6 mb-3"> :</div>
                                                <div className="cart-scroll">
                                                    <CartContent type="checkout" />
                                                </div>
                                            </div>
                                            <div>
                                                <table className="table table-borderless title-font">
                                                    <tbody>
                                                        <tr className="fs-12">
                                                            <td className="fw-5"> </td>
                                                            <td className="fw-6 text-end">{cart.total + ((cart.sale.total) ? cart.sale.total : 0)} </td>
                                                        </tr>
                                                        {
                                                            (cart.sale.total > 0) &&
                                                            <tr className="fs-12">
                                                                <td><small>{cart.sale.text ? cart.sale.text : ': '}</small></td>
                                                                <td className="fw-6 text-end">-{cart.sale.total}</td>
                                                            </tr>
                                                        }
                                                        <tr className="fs-12">
                                                            <td className="fw-5"></td>
                                                            <td className="fw-6 text-end">{(cart.total > cart.deliveryMinPrice || checkout.delivery == 1) ? '' : cart.deliveryPrice + ' '}</td>
                                                        </tr>
                                                        <tr className="fw-7">
                                                            <td className="fs-12">  </td>
                                                            <td className="fs-16 text-end">{(checkout.total) ? checkout.total : 0} </td>
                                                        </tr>
                                                    </tbody>
                                                </table>
                                                <button disabled={(cart.cart && cart.cart.length > 0 && checkout.phone && checkout.phone.length > 7) ? false : true} type="submit" className="btn btn-1 mt-3 w-100 fs-11"> </button>
                                            </div>
                                        </div>
                                        <div className="text-center fs-10 gray-3 mt-3"> " ",    <Link className="text-success" to={POLICY_ROUTE}> </Link></div>
                                    </div>
                                </div>
                            </form>
                            <Link to={CART_ROUTE} className="btn btn-2">
                                <span className="ms-2"> </span>
                            </Link>
                        </div>
                    </section>
            }
        </main>
    )
})

export default Checkout
